import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
	//
	
	state:{
		num:0,
		price:10,
		name:'',
		to:'',
		que:''
	},
	
	// 
	mutations:{
		add(state){
			state.num++;
			console.log(state.num)
		},
		setToAsQue(state){
			state.to = 'que'
			
		},
		setToAsItem(state){
			state.to = 'item'
		},
		setQueQuestion(state,res){
			
			state.que= res
		}
	},
	//vuex 
	getters:{
		const(state){
			//
			return state.num*state.price
			
		}
	},
	//
	actions:{
		testActions(context){
			//ajax
			setTimeout(function(){
				
			},2000)
			
		}
	}
})